import React from "react";
import { MenuBtn} from './general/style/style';


class MenuButton extends React.Component {
  render() {
    return (      
       <MenuBtn radius="6px" fontSize="10px" bgColor="#F5F5F5"  color="#000" marginRight="8px"
       className={this.props.active == "active" ? "menu active" : "menu"}
       onClick={()=>this.props.onClick(this.props.index)}
       >
         {this.props.buttonName}
        </MenuBtn>
       );

  }
}

export default MenuButton;
